.notFound404 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.notFound404__title{
  max-width: 922px;
  min-height: 109px;
  display: flex;
  align-items: center;
  text-align: center;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 600;
  font-size: 36px;
  line-height: 44px;
  
  /*border: red solid 1px;*/
  margin: 0px 0px 0px 0px;
}
.notFound404__text {
  display: flex;
  align-items: center;
  text-align: center;

  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 28px;

  margin: 0px 0px 20px 0px;
}

.notFound404__button-block {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 48px 0px 20px 0px;
}

.notFound404__button-main {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 168px;
  height: 40px;
  border-radius: 10px;
  padding: 8px, 20px, 8px, 20px,;
  gap: 10px;
  background-color: rgba(116, 205, 255, 1);
  /* border: rgba(116, 205, 255, 1) solid 1px; */
  border: none;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  margin: 0px 22px 0px 0px;
  
}
.notFound404__button-team {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 262px;
  height: 40px;
  border-radius: 10px;
  background-color: rgba(255, 199, 84, 1);
  border: none;
  /* border: rgba(255, 199, 84, 1) solid 1px; */
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  margin: 0px 0px 0px 0px;
}